<template>
  <div class="register-container">
    <div class="register-form">
      <h2>Ê≥®ÂÜåËøûÊé•ËÄÖËÆ∫Âùõ</h2>
      
      <form @submit.prevent="handleRegister" class="form">
        <div class="form-group">
          <label for="username">Áî®Êà∑Âêç</label>
          <input
            id="username"
            v-model="form.username"
            type="text"
            placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç"
            required
            minlength="3"
            maxlength="20"
          />
        </div>
        
        <div class="form-group">
          <label for="email">ÈÇÆÁÆ±</label>
          <input
            id="email"
            v-model="form.email"
            type="email"
            placeholder="ËØ∑ËæìÂÖ•ÈÇÆÁÆ±"
            required
          />
        </div>
        
        <div class="form-group">
          <label for="password">ÂØÜÁ†Å</label>
          <input
            id="password"
            v-model="form.password"
            type="password"
            placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"
            required
            minlength="6"
          />
        </div>
        
        <div class="form-group">
          <label for="confirmPassword">Á°ÆËÆ§ÂØÜÁ†Å</label>
          <input
            id="confirmPassword"
            v-model="form.confirmPassword"
            type="password"
            placeholder="ËØ∑ÂÜçÊ¨°ËæìÂÖ•ÂØÜÁ†Å"
            required
          />
        </div>
        
        <button type="submit" :disabled="loading" class="register-btn">
          {{ loading ? 'Ê≥®ÂÜå‰∏≠...' : 'Ê≥®ÂÜå' }}
        </button>
      </form>
      
      <!-- ÈîôËØØÊ∂àÊÅØÊòæÁ§∫ -->
      <div v-if="errorMessage" class="error-message">
        {{ errorMessage }}
      </div>
      
      <div class="form-footer">
        <p>Â∑≤ÊúâË¥¶Âè∑Ôºü <RouterLink to="/login" class="link">Á´ãÂç≥ÁôªÂΩï</RouterLink></p>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import { useAuthStore } from '@/stores/auth'

const router = useRouter()
const authStore = useAuthStore()

const form = ref({
  username: '',
  email: '',
  password: '',
  confirmPassword: ''
})

const loading = ref(false)
const errorMessage = ref('')

const validateForm = () => {
  if (form.value.username.length < 3) {
    errorMessage.value = 'Áî®Êà∑ÂêçËá≥Â∞ëÈúÄË¶Å3‰∏™Â≠óÁ¨¶'
    return false
  }
  
  if (form.value.password.length < 6) {
    errorMessage.value = 'ÂØÜÁ†ÅËá≥Â∞ëÈúÄË¶Å6‰∏™Â≠óÁ¨¶'
    return false
  }
  
  if (form.value.password !== form.value.confirmPassword) {
    errorMessage.value = '‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥'
    return false
  }
  
  errorMessage.value = ''
  return true
}

const handleRegister = async () => {
  console.log('üîÑ Ê≥®ÂÜåÊåâÈíÆË¢´ÁÇπÂáª')
  
  if (!validateForm()) {
    console.log('‚ùå Ë°®ÂçïÈ™åËØÅÂ§±Ë¥•')
    return
  }
  
  loading.value = true
  errorMessage.value = ''
  
  try {
    console.log('üìß Ê≥®ÂÜå‰ø°ÊÅØ:', {
      username: form.value.username,
      email: form.value.email
    })
    
    // Ê£ÄÊü•ÊòØÂê¶‰ΩøÁî®ÈªòËÆ§ÈÖçÁΩÆ
    const supabaseUrl = import.meta.env.VITE_SUPABASE_URL
    if (!supabaseUrl || supabaseUrl.includes('default.supabase.co')) {
      // ÂºÄÂèëÊ®°ÂºèÔºöÊ®°ÊãüÊ≥®ÂÜåÊàêÂäü
      console.log('üîß ÂºÄÂèëÊ®°ÂºèÔºöÊ®°ÊãüÊ≥®ÂÜåÊµÅÁ®ã')
      
      // Ê®°ÊãüÊ≥®ÂÜåÊàêÂäüÂêéÁöÑË∑≥ËΩ¨
      setTimeout(() => {
        console.log('‚úÖ ÂºÄÂèëÊ®°ÂºèÊ≥®ÂÜåÊàêÂäü')
        loading.value = false
        alert('Ê≥®ÂÜåÊàêÂäüÔºÅËØ∑ÈÖçÁΩÆÁúüÂÆûÁöÑSupabaseÁéØÂ¢ÉÂèòÈáè‰ª•‰ΩøÁî®ÂÆåÊï¥ÂäüËÉΩ„ÄÇ')
        router.push('/login')
      }, 1000)
      return
    }
    
    // ÁúüÂÆûÁéØÂ¢ÉÔºöË∞ÉÁî®SupabaseÊ≥®ÂÜå
    console.log('üîê Â∞ùËØïÁúüÂÆûSupabaseÊ≥®ÂÜå...')
    const result = await authStore.signUp(
      form.value.email,
      form.value.password,
      form.value.username
    )
    
    if (!result.success) {
      console.error('‚ùå Ê≥®ÂÜåÂ§±Ë¥•:', result.error)
      
      // Êèê‰æõÊõ¥ÂèãÂ•ΩÁöÑÈîôËØØÊèêÁ§∫
      if (result.error.includes('Email not confirmed')) {
        errorMessage.value = '‚úÖ Ê≥®ÂÜåÊàêÂäüÔºÅËØ∑Ê£ÄÊü•ÊÇ®ÁöÑÈÇÆÁÆ±Âπ∂ÁÇπÂáªÁ°ÆËÆ§ÈìæÊé•ÂÆåÊàêÊ≥®ÂÜå„ÄÇ'
      } else if (result.error.includes('row-level security')) {
        errorMessage.value = '‚úÖ Ê≥®ÂÜåÊàêÂäüÔºÅ‰ΩÜÁî®Êà∑ËµÑÊñôÂàõÂª∫ÂèóÈôê„ÄÇËØ∑ËÅîÁ≥ªÁÆ°ÁêÜÂëò‰øÆÂ§çÊï∞ÊçÆÂ∫ìÊùÉÈôê„ÄÇ'
      } else if (result.error.includes('User already registered')) {
        errorMessage.value = '‚ùå ËØ•ÈÇÆÁÆ±Â∑≤Ë¢´Ê≥®ÂÜåÔºåËØ∑‰ΩøÁî®ÂÖ∂‰ªñÈÇÆÁÆ±ÊàñÂ∞ùËØïÁôªÂΩï„ÄÇ'
      } else {
        errorMessage.value = result.error || 'Ê≥®ÂÜåÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï'
      }
      return
    }
    
    // Ê≥®ÂÜåÊàêÂäüÔºåÊòæÁ§∫ËØ¶ÁªÜÊàêÂäüÊ∂àÊÅØ
    console.log('‚úÖ Ê≥®ÂÜåÊàêÂäü:', result.message)
    
    // ÊòæÁ§∫ÊàêÂäüÊ∂àÊÅØÔºåÊåáÂØºÁî®Êà∑‰∏ã‰∏ÄÊ≠•Êìç‰Ωú
    if (result.message && result.message.includes('ÈÇÆÁÆ±È™åËØÅ')) {
      errorMessage.value = '‚úÖ Ê≥®ÂÜåÊàêÂäüÔºÅËØ∑Ê£ÄÊü•ÊÇ®ÁöÑÈÇÆÁÆ±Âπ∂ÁÇπÂáªÁ°ÆËÆ§ÈìæÊé•ÂÆåÊàêÊ≥®ÂÜå„ÄÇ'
    } else {
      errorMessage.value = '‚úÖ Ê≥®ÂÜåÊàêÂäüÔºÅÊ≠£Âú®Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µÈù¢...'
    }
    
    // Âª∂ËøüË∑≥ËΩ¨ÔºåËÆ©Áî®Êà∑ÁúãÂà∞ÊàêÂäüÊ∂àÊÅØ
    setTimeout(() => {
      router.push('/login')
    }, 3000)
  } catch (error) {
    console.error('‚ùå Ê≥®ÂÜåÂºÇÂ∏∏:', error)
    errorMessage.value = 'Ê≥®ÂÜåÂ§±Ë¥•ÔºåËØ∑Á®çÂêéÈáçËØï'
  } finally {
    loading.value = false
  }
}
</script>

